
// Buttons

.btn {
  padding        : 1rem 2rem;
  display        : inline-block;
  margin         : 2rem 0;
  text-transform : uppercase;
  font-family    : $heading-font-family;
  color          : $primary-color;
  font-weight    : 600;
  letter-spacing : 2px;
  background-image: linear-gradient(45deg, orange, red 100%);
  border-radius: 30px;
}
